import classNames from 'classnames';
import React from 'react';

export const formControlStyles = 'flex flex-col';

export const FormControl: React.FC<React.HTMLAttributes<HTMLDivElement>> = ({
  className,
  children,
  ...props
}) => (
  <div className={classNames(formControlStyles, className)} {...props}>
    {children}
  </div>
);
